/* Split the screen in half */
.split {
	height: 100%;
	width: 49%;
	position: fixed;
	z-index: 1;
	top: 0;
	overflow-x: hidden;
	padding-top: 20px;
}

/* Control the left side */
.left {
	left: 0;
}

/* Control the right side */
.right {
	right: 0;
}

.textDiv {
	margin-left: 1%;
}

.resultDiv {
	margin-right: 1%;
}

#txtArea {
	border: 1px black solid;
	width: 100%;
	height: 95%;
	padding-left: 8px;
	font-size: 18pt;
	resize: none;
	outline: none;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

#result {
	font-size: 35px;
	text-align: center;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	color: #2e66ce;
}

#timeList {
	font-size: 30px;
	line-height: 55px;
	text-align: left;
	padding-left: 15%;
	padding-top: 5%;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	color: rgb(0, 0, 0);
}

mark {
	background: linear-gradient(120deg, #a8c6ff 0%, #a8c6ff 100%);
	background-repeat: no-repeat;
	background-size: 100% 20%;
	background-position: 0 90%;
	color: black;
}

#invalidMark {
	background: linear-gradient(120deg, #e4a0a1 0%, #e4a0a1 100%);
	background-repeat: no-repeat;
	background-size: 100% 20%;
	background-position: 0 90%;
	color: black;
}
